<script>
import { smoothScrollToUp } from "../assets/js/helpers";

export default {
  methods: {
    vueOnScroll() {
      var prev = window.pageYOffset;
      const links = document.querySelector("ul");
      const header = this.$refs.headRef; // assign the reference in variable
      const logo = this.$refs.logo;
      const arrow = this.$refs.arrow;
      const arrow2 = this.$refs.arrow2;
      const arrow3 = this.$refs.arrow3;
      const button = this.$refs.button;
      window.addEventListener("scroll", () => {
        var curr = window.pageYOffset;
        if (prev > curr) {
          header.classList.add("scrolled");
          header.classList.remove("scrollDown");
          logo.classList.add("logo2");
          logo.classList.remove("logo1");
          arrow.style.fill = "#404145";
          arrow2.style.fill = "#404145";
          arrow3.style.fill = "#404145";
          button.classList.add("button2");
          button.classList.remove("button1");
        } else {
          header.classList.add("scrollDown");
          header.classList.remove("scrolled");
        }
        if (curr === 0) {
          header.classList.remove("scrollDown");
          header.classList.remove("scrolled");
          logo.classList.add("logo1");
          logo.classList.remove("logo2");
          arrow.style.fill = "#FFFFFF";
          arrow2.style.fill = "#FFFFFF";
          arrow3.style.fill = "#FFFFFF";
          button.classList.add("button1");
          button.classList.remove("button2");
        }
        prev = curr;
      });
    },
    logger() {
      document.body.scrollBy({
        behavior: "smooth",
        top: 0,
      });
    },
  },
  mounted() {
    // run the function when the component's mount
    this.vueOnScroll();
  },
};
</script>

